	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>John Medina said, \"what you do and learn in life physically changes what your\nbrain looks like - it literally rewires it.\"</p>\n</blockquote>\n<p>You are learning something new everyday. Whether it be a new skill, new way of\ndoing something, discovering something you haven't encountered before, exploring\na new area or meeting new people and learning about their lives and behaviors.\nThis started from birth and will follow you through your adult years.</p>\n<p>The plasticity of your brain is stronger as a child but remains plastic in your\nadult years. Your brain is not static or rigid thus keeps changing as it absorbs\nmore information.</p>\n<p>When you think, a chemical reaction occurs in your brain. The more you think, feel\nand act in the same way, the more it will stick.</p>\n<p>So as your days increment, you add on to old behaviors, thought patterns, feelings\nand beliefs. You can cling to them subconsciously making you unaware of your\nbehavior as it is a reflex - an automatic response.</p>\n<p>Our brains become fragmented and filled with spam and malware which we need to\ndefragment and clean up to keep our precious hardware safe. How about the idea\nof rewiring the way our brain works to change our behaviors so that we can\nlive healthier lives.</p>\n<p>I use the term healthier in the sense that we are able to get out of depression\neasier and cope better with anger, stress and anxiety. We <strong>let go of unhealthy\nbehaviors</strong>. The idea is to make us healthier so that we can be more productive,\napproachable and calmer.</p>\n<p>I don't think it's healthy being overly positive or overly negative. We need\nto balance the spectrum - yin and yang - synergy. That is why I prefer the term\nhealthier over positive.</p>\n<p>If you have ever tried to break a habit like smoking, drinking, biting your nails,\nwhatever your flavor; you will know how hard it is. The idea behind this series\nof articles called <strong>Rewire Your Brain</strong> is to document and share some of the\ntechniques and models available to help you successfully (hopefully) break\nbad habits and behaviors.</p>\n<p>I want to get out of the victim role where there is blame and disadvantage and\ninto the creator role where I use my circumstances to my advantage to control\nmy present destiny.</p>\n<p>I will be exploring concepts like <strong>activating neuro pathways</strong>, <strong>the Satir Change\nModel</strong>, <strong>Neuroplasticity</strong> and more. A lot of concepts overlap which deepens\nthe ideas behind these techniques and models.</p>\n<p>I have a deep personal interest in this topic as I desperately want to rewire\nmy brain for a healthier approach to life. I do hope you benefit from this too.</p>\n<hr>\n<h2 id=\"references\"><a href=\"#references\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>References</h2>\n<ul>\n<li><a href=\"https://www.youtube.com/watch?v=LNHBMFCzznE\">After watching this, your brain will not be the same</a>,\nLara Boyd at TEDxVancouver</li>\n<li><a href=\"https://www.mindbodygreen.com/0-11762/5-ways-to-rewire-your-brain-for-meaningful-life-changes.html\">5 Ways To Rewire Your Brain For Meaningful Life Changes</a>,\nMindBodyGreen, Dr. Hilary Stokes</li>\n</ul>","timeToRead":2,"excerpt":"John Medina said, \"what you do and learn in life physically changes what your\nbrain looks like - it literally rewires it.\" You are learning…","frontmatter":{"title":"Rewire your brain (Part 1) - Introduction","cover":"https://picsum.photos/1600/800/?image=883","date":"2017-05-22 20:03:54 +0200","category":null,"tags":["Mindset","Productivity"]},"fields":{"slug":"/blog/rewire-your-brain-1"}},"prev":{"excerpt":"Dr. Hilary Stokes says that we have trillions of brain cells, resulting in\nthousands (if not millions) of strings of lights (chemical reactions) correlating with our habits\nin all areas of our life…","timeToRead":4,"frontmatter":{"title":"Rewire your brain (Part 2) - Activate neuro pathways","cover":"https://picsum.photos/1600/800/?image=881","date":"2017-05-25 20:03:54 +0200"},"fields":{"slug":"/blog/rewire-your-brain-2"}},"next":{"excerpt":"\"Your beliefs don't make you a better person, our behavior does.\" I have a far way to go on my journey. My journey of self-discovery and healing.\nIt's a life-long journey and sometimes it's a real…","timeToRead":4,"frontmatter":{"title":"Practice what you preach","cover":"https://picsum.photos/1600/800/?image=129","date":"2017-05-19 18:29:22 +0200"},"fields":{"slug":"/blog/practice-what-you-preach"}}},"pathContext":{"slug":"/blog/rewire-your-brain-1","total":57,"prev":"/blog/rewire-your-brain-2","next":"/blog/practice-what-you-preach"}}

